<h1 id="git" tabindex="-1"><a class="header-anchor" href="#git" aria-hidden="true">#</a> GIT</h1>
<h2 id="description" tabindex="-1"><a class="header-anchor" href="#description" aria-hidden="true">#</a> Description</h2>
<p>C'est un gestionnaire de dépendances qui permet de faciliter le développment d'applications.</p>
<h2 id="utilisation" tabindex="-1"><a class="header-anchor" href="#utilisation" aria-hidden="true">#</a> Utilisation</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">git</span> branch dev-diene
<span class="token function">git</span> checkout dev-diene
<span class="token function">git</span> merge master
<span class="token function">git</span> push --set-upstream origin dev-diene
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><h2 id="workflow" tabindex="-1"><a class="header-anchor" href="#workflow" aria-hidden="true">#</a> Workflow</h2>
<p>master = master<br>
develop = beta  test<br>
dev = developper</p>
<h2 id="recuperation-des-mises-a-jour" tabindex="-1"><a class="header-anchor" href="#recuperation-des-mises-a-jour" aria-hidden="true">#</a> Récupération des mises à jour</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">git</span> checkout develop
<span class="token function">git</span> pull
<span class="token function">npm</span> <span class="token function">install</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p>permet de récupérer les modifications de la branche develop</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">git</span> checkout dev-diene
<span class="token function">git</span> merge develop
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h2 id="git-1" tabindex="-1"><a class="header-anchor" href="#git-1" aria-hidden="true">#</a> Git</h2>
<h3 id="creer-un-nouveau-repo" tabindex="-1"><a class="header-anchor" href="#creer-un-nouveau-repo" aria-hidden="true">#</a> Créér un nouveau répo</h3>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token builtin class-name">echo</span> <span class="token string">"# senecolo4"</span> <span class="token operator">>></span> README.md
<span class="token function">git</span> init
<span class="token function">git</span> <span class="token function">add</span> README.md
<span class="token function">git</span> commit -m <span class="token string">"first commit"</span>
<span class="token function">git</span> remote <span class="token function">add</span> origin https://github.com/desmond8500/senecolo4.git
<span class="token function">git</span> push -u origin master
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><h3 id="lier-a-un-repo-qui-existe" tabindex="-1"><a class="header-anchor" href="#lier-a-un-repo-qui-existe" aria-hidden="true">#</a> Lier à un répo qui existe</h3>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">git</span> remote <span class="token function">add</span> origin https://github.com/desmond8500/senecolo4.git
<span class="token function">git</span> push -u origin master
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div>